function slider() {
  // Slider


  // Ð¿Ð°ÑÐ°Ð¼ÐµÑÑ ÑÐµÐºÑÑÐµÐ³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°
  let slideIndex = 1,

    slides = document.querySelectorAll('.slider-item'),
    prev = document.querySelector('.prev'),
    next = document.querySelector('.next'),
    dotsWrap = document.querySelector('.slider-dots'),
    dots = document.querySelectorAll('.dot');

  showSlides(slideIndex);

  function showSlides(n) {

    // ÑÑÐ¾Ð±Ñ ÐºÐ¾Ð³Ð´Ð° ÐºÐ¾Ð½ÑÐ°Ð»Ð¸ÑÑ ÑÐ»Ð°Ð¹Ð´Ñ, Ð¼Ñ Ð¿ÐµÑÐµÑÐ¾Ð´Ð¸Ð»Ð¸ Ðº Ð¿ÐµÑÐ²Ð¾Ð¼Ñ, Ð¸Ð»Ð¸ Ðº Ð¿Ð¾ÑÐ»ÐµÐ´Ð½ÐµÐ¼Ñ, ÐµÑÐ»Ð¸ Ð½Ð°Ð¶Ð¸Ð¼Ð°ÐµÐ¼ Ð½Ð°Ð·Ð°Ð´
    if (n > slides.length) {
      slideIndex = 1;
    }
    if (n < 1) {
      slideIndex = slides.length;
    }

    slides.forEach((item) => item.style.display = 'none');
    // Ð¼Ð¾Ð¶Ð½Ð¾ Ð·Ð°Ð¿Ð¸ÑÐ°ÑÑ ÐµÑÐµ ÑÐ°Ðº, Ð½Ð¾ Ð¿ÐµÑÐ²Ð°Ñ Ð·Ð°Ð¿Ð¸ÑÑ Ð±Ð¾Ð»ÐµÐµ ÑÐ¾Ð²ÑÐµÐ¼ÐµÐ½Ð½Ð°
    // for (let i = 0; i < slides.lenght; i++) {
    // 	slides[i].style.display = 'none';
    // }
    dots.forEach((item) => item.classList.remove('dot-active'));

    slides[slideIndex - 1].style.display = 'block';
    dots[slideIndex - 1].classList.add('dot-active');
  }

  function plusSlides(n) {
    showSlides(slideIndex += n);
  }

  function currentSlide(n) {
    showSlides(slideIndex = n);
  }

  prev.addEventListener('click', function () {
    plusSlides(-1);
  })

  next.addEventListener('click', function () {
    plusSlides(1);
  });


  dotsWrap.addEventListener('click', function (event) {
    for (let i = 0; i < dots.length + 1; i++) {
      if (event.target.classList.contains('dot') && event.target == dots[i - 1]) {
        currentSlide(i);
      }
    }
  });

}

export default slider;